
body {
    font-family: 'Arial', sans-serif;
    background-color: #f7f7f7;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;

}

.mainpage {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.question {
    width: 50rem;
    height: 82vh;
    padding: 2rem;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 3px 5px 10px rgba(0, 0, 0, 0.1);
    margin: 1rem 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.question h2 {
    margin-top: 0;
    margin-bottom: 1.5rem;
    background-color: orangered;
    padding: 1rem;
    border-radius: 5px;
    text-align: center;
    font-size: 1rem;
    color: white;
    box-shadow: 3px 5px 10px rgba(0, 0, 0, 0.1);
    cursor: default;
}


.navigation {
    font-size: 1rem;
    cursor: default;
    display: flex;
    gap: 30px;
}

.nav {
    margin-top: 0;
    margin-bottom: 1.5rem;
    background-color: grey;
    padding: 1rem;
    border-radius: 5px;
    text-align: center;
    font-size: 1rem;
    color: white;
    box-shadow: 3px 5px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
}

.nav[disabled] {
    background-color: #ccc;
    cursor: not-allowed;
}

.answer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 1rem;

}


.answer label:nth-child(-n+2) {
    margin-bottom: 1rem;
}

.answer label input {
    margin-right: 10px;
    transform: scale(1.5);
}

.answer label:hover {
    background-color: rgba(220, 220, 220, 0.99);
}

.answer-label.correct {
    background-color: green;
    color: white;
}

.answer-label.incorrect {
    background-color: red;
    color: white;
}

.answer label {
    flex: 1 1 45%;
    display: flex;
    align-items: center;
    background-color: #e6e7cc;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
    cursor: pointer;

}




img {

    width: 45%;
    height: auto;
    margin-bottom: 1.5rem;
    border-radius: 10px;
    box-shadow: 3px 5px 10px rgba(0, 0, 0, 0.2);
    margin-left: calc(30%) ;
}


.resultsWindow {
    background-color: #e6e7cc;
    border-radius: 10px;
    box-shadow: 3px 5px 10px rgba(0, 0, 0, 0.1);
}

.winner, .nah, .loser {
    display: grid;
}

.winner h3 {
    color: green;
}

.loser h3 {
    color: red;
}


@media (max-width: 768px) {
    .question {
        width: 90%;
    }

    .answer {
        display: flex;
        min-width: 200px;

    }

    .answer label {
        flex: 1 1 100%;
    }

    .navigation {
        grid-gap: 1vh;
        display: grid;
    }
}